@import "src/common/styles/sass/variables";

.footerBlock {
  height: 80px;
  background-color: $bgSecondaryColor;

  .footerContainer {
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    justify-content: center;
  }

  .titleFoot {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 14px;
    line-height: 1.8;
    margin: 0 0 5px;
    color: $textSecondaryColor;
  }
}